import {
  ADD_PRODUCT,
  REMOVE_PRODUCT,
  CHANGE_PRODUCT_QUANTITY,
} from './actionTypes';

const initialState = {
  total: 0,
  products: [],
};

const calcuTotal = products =>
  products.reduce((sum, p) => {
    sum += p.price * p.quantity;
    return sum;
  }, 0);

const cartReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_PRODUCT:
      return {
        ...state,
        total: total + action.payload.price,
        products: [...state.products, action.payload],
      };
    case REMOVE_PRODUCT:
      return {
        ...state,
        total: total - action.payload.totalPrice,
        products: state.products.filter(product => product !== action.payload),
      };
    case CHANGE_PRODUCT_QUANTITY:
      // const newProducts = [...state.products].splice(
      //   action.payload.index,
      //   1,
      //   action.payload.product,
      // );

      const newProducts = [...state.products];
      const productIndex = newProducts.findIndex(
        p => p.id == action.payload.product.id,
      );
      newProducts[productIndex] = action.payload.product;

      return {
        ...state,
        total: calcuTotal(newProducts),
        products: newProducts,
      };

    default:
      return state;
  }
};

export {cartReducer};
